.row{margin-left:20px;color:white}.row .row_title{padding-top:1.5rem;font-family:'Domine', serif;font-size:larger;text-shadow:2px 1px 1px rgba(137,15,185,0.815)}.row .row_posters{display:-webkit-flex;display:flex;overflow-y:hidden;overflow-x:scroll;padding:30px}.row .row_posters .row_poster{object-fit:contain;width:100%;max-height:130px;margin-right:10px;transition:-webkit-transform 405ms;transition:transform 405ms;transition:transform 405ms, -webkit-transform 405ms}.row .row_posters .row_poster:hover{-webkit-transform:scale(1.08);transform:scale(1.08)}.row .row_posters .row_posterLarge{max-height:250px}.row .row_posters .row_posterLarge:hover{-webkit-transform:scale(1.09);transform:scale(1.09)}.row .row_posters::-webkit-scrollbar{display:none}

.banner{background-size:cover;background-position:center top;color:white;object-fit:contain;height:600px;margin:0 auto}.banner .banner_contents{margin-left:30px;padding-top:160px;height:190px}.banner .banner_contents .banner_buttons .banner_button{cursor:pointer;color:#fff;outline:none;border:none;font-family:'Domine', serif;font-weight:bold;border-radius:0.2vw;padding:0.5rem 2rem 0.5rem 2rem;margin-right:1rem;background-color:rgba(51,51,51,0.5)}.banner .banner_contents .banner_buttons .banner_button:hover{color:#000;background-color:#e6e6e6;transition:all 0.2s}.banner .banner_contents .banner_title{font-size:3rem;font-family:'Domine', serif;text-shadow:2px 1px 1px rgba(137,15,185,0.815);padding-bottom:0.3rem;font-weight:700}.banner .banner_contents .banner_description{width:45rem;line-height:1.3;padding-top:1rem;font-size:1.1rem;font-weight:lighter;font-family:'Domine', serif;text-shadow:1px 1px 1px rgba(39,32,44,0.815);max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content;max-height:-webkit-fit-content;max-height:-moz-fit-content;max-height:fit-content;letter-spacing:0.1rem;font-family:NKufi,'Netflix Sans','Helvetica Neue',Helvetica,Arial,sans-serif}.banner .banner--fadeBottom{height:18.4rem;background-image:linear-gradient(180deg, transparent, rgba(37,37,37,0.61), #111)}

*{margin:0}* .App{background-color:#111}

.nav{position:fixed;top:0;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;transition-timing-function:ease-in;transition:all 1.3s;z-index:1000;width:100%;padding-top:1.2rem;padding-bottom:20px;height:30px}.nav .nav_logo{margin-left:0.4rem;-webkit-align-self:start;align-self:start;width:8rem;height:100%;background-image:url("https://cloudfront-us-east-1.images.arcpublishing.com/gray/3HCWZMP7PFGY3OJJPFHIX5O2VI.png");background-size:cover;background-position:center;background-repeat:no-repeat;position:relative}.nav .nav_logo ::after{content:"";position:absolute;width:100%;height:100%}.nav .nav_avatar{margin-right:1rem;width:2rem;height:100%;background-image:url("https://upload.wikimedia.org/wikipedia/commons/0/0b/Netflix-avatar.png");background-size:contain;background-position:center;background-repeat:no-repeat}.navBtnBox{margin-left:auto;margin-right:1rem;max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content;display:-webkit-flex;display:flex}.navBtnBox .navBtn{cursor:pointer;border:none;width:4.6rem;height:2rem;border-radius:5px;background:red;background-color:inherit;color:gray;font-family:'Domine', serif;font-size:larger;font-weight:700;text-shadow:2px 1px 1px rgba(62,49,73,0.815)}.navBtnBox .navBtn span{color:white;font-size:0.9rem}.nav_black{background-color:#111}

.footer-div{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;max-width:100%;margin-top:100px;font-family:'Domine', serif;font-size:0.8rem;font-weight:100;text-shadow:2px 1px 1px rgba(69,16,90,0.815)}.footer-div .mediaStyles{color:gray;font-size:1.8rem}.footer-div .socialMedia{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;grid-gap:1.9rem;gap:1.9rem;width:60%}.footer-div footer{background-color:inherit;display:-webkit-flex;display:flex;width:60%;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;padding:20px}.footer-div footer a{color:gray;text-decoration:none;padding:10px;cursor:pointer;line-height:1.7}.footer-div footer .col1{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.footer-div footer .col2{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.footer-div footer .col3{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.footer-div footer .col4{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.footer-div span{margin-bottom:0.5rem;color:gray}

.page{width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-position:center;background-position-y:70px;background-repeat:no-repeat;background-size:cover;font-family:sans-serif;background-color:#000;color:#000;color:white}.signupHeader{text-align:center}.signupForm{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:1rem}.signupInput{border:1px solid grey;padding:1rem;width:20rem;margin-bottom:0.8rem}.signupBtn{cursor:pointer;border:none;width:8rem;height:2.6rem;border-radius:5px;background:red;width:22rem}.signupBtn span{color:white;font-size:1rem}

.homepage{width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-position:center;background-repeat:no-repeat;background-size:cover;font-family:sans-serif}.homepage::after{width:102%;height:102%;content:"";position:absolute;background-color:rgba(0,0,0,0.438);z-index:5}.hero{text-align:center;color:white;z-index:10}.emailBox{border:none;height:1rem;width:26rem;margin-right:1rem;margin-top:2rem;padding:0.8rem}.startBtn{cursor:pointer;border:none;width:8rem;height:2.6rem;border-radius:5px;background:red}.startBtn span{color:white;font-size:1rem}.captionTitle{color:white;z-index:10;position:absolute;margin:1rem;bottom:0;left:0}

